body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
nav{z-index:2}.logoNavbar{width:8rem}nav a.nav-link{font-weight:500;letter-spacing:.25rem;cursor:pointer;font-size:1rem}nav ul li{text-transform:uppercase}.navbar-nav>li>a{margin-right:.5rem;margin-left:.5rem;color:var(--cor-amarela) !important;text-align:right;border-top:.1rem solid var(--cor-preta)}navbar-brand{max-width:20rem;padding:0 !important}.navbar-nav>li>a.active,.navbar-nav>li>a.active:focus{border-top:.1rem solid var(--cor-texto)}.navbar-nav>li>a:hover{border-top:.1rem solid var(--cor-texto)}@media(max-width: 768px){.logoNavbar{width:5rem}}
.header-wraper{position:relative;background:url(/static/media/wallpapper.6e36599a.jpg) no-repeat;background-size:cover;background-position:center;height:90vh}.main-info{display:flex;position:absolute;top:0;left:0;bottom:0;right:0;flex-direction:column;justify-content:center;align-items:center;text-align:center;font-family:"Roboto",sans-serif;z-index:1}.main-info h1{margin-top:0rem;font-weight:600;color:gold;text-transform:uppercase;text-shadow:5px 5px var(--fundo-escuro);box-shadow:#19395b;font-size:4rem}@media(max-width: 768px){.main-info h1{font-size:2rem !important}}.typed-text{font-size:3rem;color:var(--cor-destaque);text-transform:uppercase;margin-top:20vh;margin-left:70vw;text-shadow:5px 5px 5px var(--cor-laranja)}.main-info button{padding:1rem 1rem 1rem 1rem;border:1px solid var(--cor-destaque);text-transform:uppercase;text-decoration:none;color:var(--cor-laranja);margin-top:20vh;background:transparent}.btn-main-offer{font-family:Verdana,sans-serif;font-weight:300;border:1px solid var(--cor-vermelha);border-radius:5%;text-transform:uppercase;padding:1rem 1rem 1rem 1rem;color:var(--cor-destaque);margin:2rem;text-decoration:none}.btn-main-offer:hover{text-decoration:none;background-color:var(--cor-vermelha);color:var(--cor-destaque);transition:.2s ease-in-out}.tsparticles-canvas-el{position:absolute;height:100vh;width:100vw;z-index:1 !important}@media(max-width: 768px){.typed-text{font-size:1.5rem !important;margin-top:30vh;margin-left:0}.main-info a{margin-top:10vh;font-size:1rem !important}#tsparticles{display:none}}
#about{font-size:20px;font-family:"Roboto",sans-serif}.profile-img{width:20rem;height:20rem;border-radius:50%;padding:.3rem;border:1px solid var(--cor-laranja);object-fit:cover}.photo-wrap{display:block;text-align:center;margin-top:1rem}.aboutme{margin-top:4rem}.heading{color:#000;padding-bottom:10px;border-bottom-style:solid;border-color:var(--cor-laranja);text-transform:uppercase;border-bottom-width:3.1px;width:-webkit-fit-content;width:fit-content;width:-moz-fit-content}.textBody{text-align:justify;padding-top:2rem}@media(max-width: 468px){.profile-img{width:15rem;height:15rem}}
.experience{background-image:linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%)}.experience-wrapper{position:relative;padding:3.125rem 0;overflow:hidden}.experience h1{text-transform:uppercase;color:var(--cor-preta)}.experience-wrapper::before{position:absolute;content:"";top:0;left:50%;margin-left:-0.0625rem;width:.125rem;height:100%;background:var(--cor-cinza);z-index:1}.timeline-block{width:calc(50% + .5rem);display:flex;justify-content:space-between;clear:both}.time-line-block-right{float:right;direction:ltr}.time-line-block-left{float:left;direction:rtl}.marker{width:1rem;height:1rem;border-radius:50%;border:.3125rem solid var(--cor-cinza);background:var(--cor-laranja);margin-top:.625rem;z-index:3}.timeline-content{width:95%;padding:0 .9375rem;color:var(--cor-cinza);direction:ltr}.timeline-content h3{margin-top:.3125;color:var(--cor-laranja);font-weight:800}.titleLineL{width:95%;float:right;direction:rtl}.p-right{text-align:left}.p-left{text-align:right}
:root{--cor-destaque: #FFFF;--cor-vermelha: #FF4A3B;--cor-amarela: #DABA36;--fundo-escuro:#19395B;--cor-laranja: #E48877;--cor-preta:rgb(255,255,255,0);--cor-cinza:#9B94B5;--cor-cinza-escuro:#404040}body{font-size:20px;font-family:"Roboto",sans-serif;margin:0;width:100%;height:100%}.portfolio{display:block;text-align:center;padding-bottom:5rem;background-image:linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%)}.background{background-image:linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%)}.portfolio-h1{padding-bottom:10%;color:#E48877;color:var(--cor-laranja);text-transform:uppercase}.portfolio .card{width:18rem;color:#FFFF;color:var(--cor-destaque);height:25rem;border-bottom:.3125rem solid #E48877;border-bottom:.3125rem solid var(--cor-laranja);margin-bottom:1.875rem;transition:.3s ease-in-out;overflow:hidden}.portfolio .card:hover{color:#fff;border-bottom:.3125rem solid rgb(255,255,255,0);border-bottom:.3125rem solid var(--cor-preta);transform:scale(1.2)}.card-body{background:#404040;background:var(--cor-cinza-escuro);padding:1rem}.div-button,.button-project{border:1px solid #FF4A3B;border:1px solid var(--cor-vermelha);border-radius:.5rem;text-align:center;align-items:center;background:transparent;color:#FFFF;color:var(--cor-destaque);width:100%}.button-project:hover{background-color:#FF4A3B;background-color:var(--cor-vermelha)}.cards.description{font-size:.75rem;text-align:justify}.cards-title{font-size:1.5rem;text-align:center}.imagemPortifolio{width:100%;height:90%}@media(max-width: 768px){.portfolio .card{max-width:90%;margin-left:auto;margin-right:auto}}
.contacts{background:#353839;padding-bottom:3rem}.contacts h1{color:var(--cor-laranja);padding:1.3rem;text-transform:uppercase}.contacts p{color:#a9a9a9;font-size:1.2rem}.contato-h1{text-decoration:none}.contacts input,.contacts textarea{width:100%;box-sizing:border-box;background:none;border:0;border-bottom:.125rem solid var(--cor-cinza);border-radius:0;color:var(--cor-laranja);outline:0 !important;box-shadow:none !important;top:0;margin-bottom:1rem;padding-left:0 !important;font-size:1.5rem}.contacts textarea{padding-bottom:2rem}.contacts .container{padding:4rem 0}.contacts input:focus,.contacts textarea:focus{background:transparent;color:var(--cor-laranja);border-bottom:.125rem}.contacts textarea{height:16.2rem !important}.contact-btn{width:100%;background:transparent;margin:1rem 0 0 0}input:focus+.line,textare:focus+.line{width:100%;transition:width .5s ease-in-out}input.form-control{margin:-0.3rem 0}textarea.form-control{margin:-0.3rem 0}textarea:focus+.line,textare:focus+.line{width:100%;transition:width .5s ease-in-out}.bd-placeholder-img{font-size:1.125rem;text-anchor:middle;-webkit-user-select:none;user-select:none;-ms-user-select:none}.line{position:relative;top:-0.8rem;width:0;height:.125rem;background:var(--cor-laranja);display:inline-block}@media(max-width: 768px){.contacts{overflow-x:hidden}.contacts .container{padding:0 1rem}.contacts p{padding:0 .4rem}}@media(min-width: 768px){.bd-placeholder-img-lg{font-size:3.5rem}}
footer{z-index:4;position:-webkit-sticky;position:sticky}#footer h3{font-size:1.5rem;text-align:center;align-items:center;margin:2rem}#footer h3::first-letter{color:var(--cor-vermelha)}p{padding:.25rem;text-align:center;align-items:center;font-size:1rem}.d-flex{display:flex;flex-flow:row wrap;justify-content:center}.assinatura{width:70px}.footer{background:#000;color:#a9a9a9;padding:3rem}.footer a{text-decoration:none;color:#a9a9a9}.footer a:hover{text-decoration:none;color:var(--cor-laranja);cursor:pointer}.footer p{margin-bottom:0}.redeSocial{width:40px;margin:.5rem}.redeSocial:hover{transform:scale(1.2)}@media(max-width: 768px){.footer-nav{font-size:1rem}.col-nav-footer{margin:3rem;text-align:center;align-items:center}#footer h3{font-size:1.2rem;text-align:center;align-items:center;margin:2rem}#footer h3::first-letter{color:var(--cor-vermelha)}#footer p{font-size:.7rem}}
